<template>
  <div class="middle">
    <h1 class="label" :style="{'color': textColor}">{{ label }}</h1>
    <div class="time" :style="{'color': textColor}">
      <div class="date-box">{{ endTime==='0'? 0 : durationFormatter.dd }}</div>
      <span>天</span>
      <div class="date-box">{{ endTime==='0'? 0 : durationFormatter.hh }}</div>
      <span>时</span>
      <div class="date-box">{{ endTime==='0'? 0 : durationFormatter.mm }}</div>
      <span>分</span>
      <div class="date-box">{{ endTime==='0'? 0 : durationFormatter.ss }}</div>
      <span>秒</span>
    </div>
  </div>
</template>
<script>
import dayjs from 'dayjs'
import { timestampToTime } from '@/utils/tools'
import { stringify } from 'querystring'
// import { onMounted, onUnmounted, ref, toRefs, computed, defineEmits, withDefaults, defineProps, watch } from 'vue'

  // fontSize: ['2.2rem', '1.2rem', '3rem'],
export default {
  props: {
    label: {
      type: String
    },
    endTime: {
      type: String
    },
    textColor: {
      type: String,
      default: '#121A26'
    },
  },
  data() {
    return {
      timeLeft: dayjs(+this.endTime).diff(dayjs(Date.now()), 'seconds'),
      timer: null
    }
  },
  watch: {
    'endTime': {
      handler(newValue) {
        this.timeLeft = dayjs(+newValue).diff(dayjs(Date.now()), 'seconds')
      }
    }
  },
  computed: {
    durationFormatter() {
      let dd = '0'
      let hh = '00'
      let mm = '00'
      let ss = '00'
      if (this.timeLeft > 0) {
        const time = this.timeLeft
        // 天
        dd = Math.floor(time / 60 / 60 / 24)
        // 时
        const h = Math.floor(time / 60 / 60 % 24)
        hh = h < 10 ? '0' + h : h + ''
        // 分
        const m = Math.floor(time / 60 % 60)
        mm = m < 10 ? '0' + m : m + ''
        // 秒
        const s = Math.floor(time % 60)
        ss = s < 10 ? '0' + s : s + ''
      }
      return { dd, hh, mm, ss }
    }
  },
  methods: {
    getTime() {
      this.timer && clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        if (this.timeLeft > 0) {
          --this.timeLeft
          this.getTime() // 递归调用
        } else {
          this.$emit('time-end', '0')
        }
      }, 1000)
    }
  },
  mounted() {
    this.getTime()
  },
  unmounted() {
    if (this.timer) {
      clearTimeout(this.timer)
    }
  }
}
</script>
<style lang='scss' scoped>
.date-box {
  width: 42px;
  height: 38px;
  line-height: 38px;
  background-color: #fff;
  color:#32cdcd;
  border-radius: 8px;
  border: 1px solid #d0d3d6;
  text-align: center;
  font-size: 24px;
}
.middle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  user-select: none;
}
.label {
  font-size: 24px;
  margin-bottom: 20px;
  font-weight: 400;
}

.time {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
}

.time span {
  padding: 0 14px;
  font-size: 20px;
  line-height: 38px;
}

@media (max-width: 740px) {
  .label {
    font-size: 1.7rem;
  }
  .time span {
    padding: 0 16px;
    font-size: 0.8rem;
  }
  .time span div {
    font-size: 2rem;
  }
}

</style>
