<template>
  <div>
    <el-dialog
      :visible.sync="helloAppliVisibleProps"
      title="欢迎报名" 
      :width="isMobile?'85%':'467px'"
      center
      :close-on-click-modal="false"
      @close="helloAppliCloseFn"
    >
      <div class="hello-info">
        <el-form
          ref="bindFormRef"
          :model="bindForm"
          :rules="rules"
          label-width="70px"
          label-position="left"
          style="width: 350px;margin: 0 auto"
        >
          <el-form-item label="手机号:" prop="phone">
            <el-input size="mini" v-model="bindForm.phone" placeholder="请输入手机号" :clearable="true" style="width:100%" />
          </el-form-item>
          <el-form-item label="验证码:" prop="code">
            <el-row type="flex" justify="space-between">
              <el-input size="mini" v-model="bindForm.code" placeholder="请输入验证码" :clearable="true" style="width:130px;" />
              <el-button 
                :disabled="!(captchaTime===60)" 
                plain 
                @click="getCaptchaCodeFn" 
                style="color: #32cdcd;border: 1px solid #32cdcd;margin-top: 5px;height: 28px;padding: 3px 15px;"
              >{{ captchaText }}</el-button>
            </el-row>
          </el-form-item>
        </el-form>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="bindFn" style="padding: 7px 70px;margin-right: 35px;" size="mini">绑定</el-button>
        </span>
      </template>
    </el-dialog>
    <el-dialog
      :visible.sync="appliVisible"
      title="填写报名表" :width="isMobile?'90%':'500px'"
      center
      :close-on-click-modal="false"
      @close="helloAppliCloseFn"
    >
      <div class="hello-info">
        <el-form
          ref="ruleFormRef"
          :model="ruleForm"
          :rules="rules"
          label-width="80px"
          label-position="left"
          style="width: 380px;margin: 0 auto"
        >
          <el-form-item label="手机号" prop="phone">
            <el-input v-model="ruleForm.phone" placeholder="请输入手机号" :clearable="true" disabled/>
          </el-form-item>
          <el-form-item label="姓名" prop="name">
            <el-input v-model="ruleForm.name" placeholder="请输入真实姓名" :clearable="true" />
          </el-form-item>
          <el-form-item label="性别" prop="sex" style="text-align:left">
            <el-radio-group v-model="ruleForm.sex">
              <el-radio :label="1">男</el-radio>
              <el-radio :label="0">女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="公司" prop="company">
            <el-input v-model="ruleForm.company" placeholder="请输入公司/组织名称" :clearable="true" />
          </el-form-item>
          <el-form-item label="职务" prop="position">
            <el-input v-model="ruleForm.position" placeholder="请输入所在公司的职务" :clearable="true" />
          </el-form-item>
          <el-form-item label="微信" prop="wechat">
            <el-input v-model="ruleForm.wechat" placeholder="请输入有效微信号" :clearable="true" />
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="ruleForm.email" placeholder="邮箱用来接收邀请函，请输入准确邮箱" :clearable="true" />
          </el-form-item>
          <el-form-item label="兴趣标签" prop="labels">
            <el-checkbox-group v-model="ruleForm.labels" size="mini" style="text-align: left;padding-top: 10px;">
              <template v-for="item in tagList">
                <el-checkbox size="mini" :label="item" border style="margin: 0 4px 0 0;">{{item.label}}</el-checkbox>
              </template>
            </el-checkbox-group>
          </el-form-item>
        </el-form>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="submitForm">提交</el-button>
        </span>
      </template>
    </el-dialog>
    <el-dialog
      :visible.sync="applyStatusVisible"
      title="" :width="isMobile?'85%':'467px'"
      center
      :close-on-click-modal="false"
      @close="applyStatusCloseFn"
    >
      <div v-if="applyedShow" class="applied-box">
        <div>
          <span>已报名</span>
          <i class="el-icon-circle-check" style="font-size: 50px;color: #32cdcd;"></i>
        </div>
        <p>您已报名成功，主办方需要审核您的参会资格。审核通过后会发送邀请函到您的邮箱，请注意查收。</p>
      </div>
      <div v-else class="apply-success">
        <div>
          <i class="el-icon-circle-check" style="font-size: 50px;color: #32cdcd;"></i>
        </div>
        <p>报名成功</p>
        <p>恭喜您报名成功，主办方需要审核您的参会资格。审核通过后会发送邀请函到您的邮箱，请注意查收。</p>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { apiPostLoginBysms, apiPostApplyStatus, apiPostMemberAdd, apiActivotyTags, apigetAccountByPhone, apiGetSms } from '@/apis/api'
const validatePhone = (rule, value, callback) => {
  if (!value) {
    callback(new Error('请输入手机号！'))
  } else if (!/^1[3456789]\d{9}$/.test(value)) {
    callback(new Error('请输入正确的手机号'))
  } else {
    callback()
  }
}
var validateEmail = (rule, value, callback) => {
  // 验证邮箱的正则表达式
  const regEmail = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
  if (!regEmail.test(value)) {
    callback(new Error("请输入正确的邮箱"))
  } else {
    callback()
  }
}
export default {
  props: {
    helloAppliVisibleProps: false,
  },
  data() {
    return {
      isMobile: false,
      appliVisible: false,
      captchaTime: 60,
      captchaText: '获取验证码',
      applyStatusVisible: false,
      ruleForm: {
        activityId: '',
        labels: [],
        name: '',
        roleType: 1,
        phone: '',
        sex: null,
        company: '',
        position: '',
        wechat: '',
        email: '',
      },
      rules: {
        code: [{ required: true, message: '请输入验证码', trigger: 'blur' }],
        name: [{ required: true, message: '请输入真实姓名', trigger: 'blur' }],
        phone: [{ required: true, message: '请输入手机号', trigger: 'blur' }, { validator: validatePhone, trigger: 'blur' }],
        sex: [{ required: true, message: '请选择性别', trigger: 'change' }],
        company: [{ required: true, message: '请输入公司名称', trigger: 'blur' }],
        position: [{ required: true, message: '请输入所在公司的职位', trigger: 'blur' }],
        wechat: [{ required: true, message: '请输入有效微信号', trigger: 'blur' }],
        email: [{ required: true, message: '请输入邮箱', trigger: 'blur' }, { validator: validateEmail, trigger: 'blur' }],
        // 审核弹窗
        approvalStatus: [{ required: true, message: '请选择审核结果', trigger: 'change' }],
        refuseReason: [{ required: true, message: '请输入拒绝原因', trigger: 'blur' }],
      },
      bindForm: {
        phone: '',
        code: ''
      },
      applyedShow: false,
      notApply: {
        userId: '',
        activityId: ''
      },
      tagList: []
    }
  },
  watch: {
    'appliVisible': {
      handler(newVal) {
        if(newVal) {
          this.getLabelsList();
        }
      }
    }
  },
  methods: {
    helloAppliCloseFn() {
      this.$refs.bindFormRef.resetFields();
      this.$emit('closeDialog', 'helloAppliVisible');
    },
    applyStatusCloseFn( ){
      this.applyStatusVisible = false;
    },
    // 新增白名单
    async submitForm() {
      console.log(this.ruleForm)
      await this.$refs.ruleFormRef.validate((valid, fields) => {
        if (valid) {
          const params = Object.assign(this.ruleForm, {
            activityId: this.notApply.activityId,
            userId: this.notApply.userId,
            enterOrigin: 1 //  0 - 管理员添加, 1 - 自主报名
          })
          apiPostMemberAdd(params).then((res) => {
            if (res) {
              this.$refs.ruleFormRef.resetFields()
              this.appliVisible = false
              this.applyedShow = false
              this.applyStatusVisible = true
            }
          })
        } else {
          console.log('error submit!', fields)
        }
      })
    },
    // 获取活动兴趣标签
    getLabelsList() {
      apiActivotyTags({ activityId: this.$store.state.activityId }).then((res) => {
        if (res) {
          this.tagList = res.result
        }
      })
    },
    // 绑定
    bindFn () {
      const params = {
        ...this.bindForm,
      }
      apiPostLoginBysms(params).then((res) => {
        if (res.code === 1000) {
          const data = res.result;
          window.localStorage.setItem('userInfo', JSON.stringify(data));
          this.$store.commit('setUserInfo', JSON.stringify(data));
          this.ruleForm.phone = JSON.parse(JSON.stringify(this.bindForm.phone));
          this.helloAppliCloseFn();
          this.isApply();
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    // 根据手机号查询账户信息
   getAccountInfo (phone){
      if (!phone) return
      apigetAccountByPhone({ phone }).then((res) => {
        if (res) {
          const data = res.result;
          this.ruleForm = Object.assign(this.ruleForm, {
            labels: data.labels,
            name: data.name,
            roleType: data.roleType,
            sex: data.sex,
            company: data.company,
            position: data.position,
            wechat: data.wechat,
            email: data.email,
          })
        }
      })
    },
    // 是否报名
    isApply () {
      const params = {
        code: this.$route.query.code
      }
      apiPostApplyStatus(params).then((res) => {
        // 已报名
        if (res.result.submit) {
          this.applyedShow = true
          this.applyStatusVisible = true
        } else { // 未报名
          this.getAccountInfo(); // 反显用户信息
          this.notApply.activityId = res.result.activityId
          this.notApply.userId = res.result.userId
          this.applyedShow = false
          this.appliVisible = true
        }
      })
    },
    // 勾选标签
    onTagChange (val) {
      const index = this.ruleForm.labels?.findIndex(item => item.id === val.id)
      if (index === -1) {
        this.ruleForm.labels.push({ id: val.id, label: val.label })
      } else {
        this.ruleForm.labels.splice(index, 1)
      }
    },
    // 获取验证码
    getCaptchaCodeFn () {
      if(!this.bindForm.phone)  {
        this.$message.error('请输入手机号');
        return
      }
      if(!/^1[3456789]\d{9}$/.test(this.bindForm.phone))  {
        this.$message.error('请输入正确的手机号');
        return
      }
      apiGetSms({phone: this.bindForm.phone}).then(res => {
        if(res && res.code === 1000) {
          this.$message.success('验证码发送成功')
          const timer = setInterval(() => {
          this.captchaTime--
          this.captchaText = this.captchaTime + 'S后重新发送'
          if (this.captchaTime === 0) {
            clearInterval(timer)
            this.captchaText = '重新发送验证码'
            this.captchaTime = 60
          }
        }, 1000)
        } else {
          this.$message.error(res.message)
        }
      })
      
    },
    // 是否是手机端
    isMobileFn () {
      const isMob = /Android|webOS|iPhone|iPod|BlackBerry|iPad/i.test(
        navigator.userAgent
      )
      return isMob
    }
  },
  mounted() {
    this.isMobile = this.isMobileFn()
  }
  
}
</script>
<style lang='scss' scoped>
 .el-button {
  border-radius: 4px;
}
.hello-info {
  text-align: center;
  // width: 290px;
  margin: 0 auto;
  >div {
    margin-bottom: 20px;
    span {
      display: inline-block;
      width: 50px;
      margin-right: 20px;
      line-height: 32px;
    }
    >div {
      vertical-align: middle;
    }
  }
  /deep/.el-input {
    width: 100%;
  }
}
.applied-box {
  color: #2E2C34;
  >div:nth-of-type(1) {
    text-align: center;
    margin-bottom: 30px;;
    >span {
      font-size: 26px;
      vertical-align: middle;
    }
    >i {
      vertical-align: middle;
    }
  }
  >p {
    line-height: 20px;
    font-size: 16px;
    text-align: center;
  }
}
.apply-success {
  color: #2E2C34;
  text-align: center;
  >p:nth-of-type(1) {
    font-size: 24px;
    margin-bottom: 30px;
  }
  >p:nth-of-type(2) {
    line-height: 20px;
    font-size: 14px;
    text-align: center;
  }
}
.el-form {
  /deep/.el-input {
    input{
      border-radius: 8px!important;
    }
  }
}
</style>