<template>
  <div style="width: 100%;height:100%;">
    <div v-if="!isMobile" class="preview">
      <el-row type="flex" class="activity-top">
        <el-col :span="12" class="activity-top-left">
          <div style="position:relative;">
            <el-image v-if="buttonText==='进入活动'" class="activity-status" style="width: 41px; height: 55px" :src="require('@/assets/images/activity_ing.png')" fit="fill" />
            <el-image v-else-if="buttonText==='活动已结束'" class="activity-status" style="width: 41px; height: 55px" :src="require('@/assets/images/activity_end.png')" fit="fill" />
            <el-image v-else class="activity-status" style="width: 41px; height: 55px" :src="require('@/assets/images/activity_not_started.png')" fit="fill" />
            <el-image style="width: 100%; height: 270px" :src="ruleForm.bgUrl" fit="fill" />
          </div>
          <el-row type="flex" justify="space-between">
            <div>
              <p>{{ ruleForm.name }}</p>
              <p>活动时间：{{ ruleForm.activityFrom }} — {{ ruleForm.activityTo }}</p>
            </div>
            <div>
              <el-image style="width: 65px; height: 65px" :src="ruleForm.visitQrCode" fit="fill" />
              <p>扫码参会</p>
            </div>
          </el-row>
        </el-col>
        <el-col :span="12" class="activity-top-right">
          <div>
            <countDown :endTime="countDownTime" @time-end="timeEnd" :label="labelText" />
              <div class="activity-btn not-start-btn" v-if="buttonText==='报名未开始'">报名未开始</div>
              <div class="activity-btn active-btn" v-if="buttonText==='报名'" @click="appliClickFn">报名</div>
              <div class="activity-btn not-start-btn" v-if="buttonText==='活动未开始'">活动未开始</div>
              <div class="activity-btn active-btn" v-if="buttonText==='进入活动'" @click="toLogin">进入活动</div>
              <div class="activity-btn not-start-btn" v-if="buttonText==='活动已结束'">活动已结束</div>
              <div class="activity-btn not-start-btn" @click="toLogin">测试进入活动登录</div>
              <div class="activity-btn active-btn" @click="appliClickFn">测试报名</div>
          </div>
          <div>
            <p>报名时间：{{ ruleForm.enterFrom }} — {{ ruleForm.enterTo }}</p>
            <p>主办单位：{{ ruleForm.sponsorName }}</p>
            <p>联系方式：{{ ruleForm.sponsorPhone }}</p>
            <p>直播链接地址：{{ ruleForm.liveUrl || '' }}
              <i v-if="ruleForm.liveUrl" class="el-icon-document-copy" @click="onCopy"></i>
            </p>
          </div>
        </el-col>
      </el-row>
      <div class="activity-tag"><div></div><span>活动简介</span></div>
      <div class="activity-tag-content">
        <p>{{ ruleForm.description }}</p>
      </div>
      <div v-if="ruleForm.detailUrl" class="activity-tag"><div></div><span>活动详情</span></div>
      <div v-if="ruleForm.detailUrl" class="activity-tag-content">
        <el-image style="width: 100%;" :src="ruleForm.detailUrl" />
      </div>
    </div>
    <div v-if="isMobile" class="mob-activity">
      <div>
        <!-- <div /> -->
        <h1>{{ ruleForm.name }}</h1>
        <p>{{ ruleForm.description }}</p>
        <div style="position: relative;">
          <el-image style="width: 100%; height: 190px" :src="ruleForm.bgUrl" fit="fill" />
          <div class="mob-countdown" style="bottom: 20px;">
            <countDown :endTime="countDownTime" @time-end="timeEnd" :label="labelText" :textColor="'#fff'" :mFontSize="['1.7rem', '0.8rem', '2rem']" />
            <div class="activity-btn not-start-btn" v-if="buttonText==='报名未开始'">报名未开始</div>
            <div class="activity-btn active-btn" v-if="buttonText==='报名'" @click="appliClickFn">报名</div>
            <div class="activity-btn not-start-btn" v-if="buttonText==='活动未开始'">活动未开始</div>
            <div class="activity-btn active-btn" v-if="buttonText==='进入活动'" @click="toLogin">进入活动</div>
            <div class="activity-btn not-start-btn" v-if="buttonText==='活动已结束'">活动已结束</div>
            <!-- <div class="activity-btn not-start-btn" @click="test">测试进入活动登录</div> -->
            <!-- <div class="activity-btn active-btn" @click="appliClickFn">测试报名</div> -->
          </div>
        </div>
        <div class="time-info">
          <div style="width: calc(100% - 80px);margin-top: 3px;">
            <p>活动时间：{{ ruleForm.activityFrom }} — {{ ruleForm.activityTo }}</p>
            <p>报名时间：{{ ruleForm.enterFrom }} — {{ ruleForm.enterTo }}</p>
            <p>主办单位：{{ ruleForm.sponsorName }}</p>
            <p>联系方式：{{ ruleForm.sponsorPhone }}</p>
            <p>直播链接地址：{{ ruleForm.liveUrl || '' }}</p>
          </div>
          <div>
            <el-image style="width: 80px; height: 80px" :src="ruleForm.visitQrCode" fit="fill" />
            <p>扫码参会</p>
          </div>
        </div>
        <div v-if="ruleForm.detailUrl" class="activity-tag"><div></div><span>活动详情</span></div>
        <div v-if="ruleForm.detailUrl" class="activity-tag-content" style="margin-top:15px;">
          <el-image style="width: 100%;" :src="ruleForm.detailUrl" />
        </div>
      </div>
    </div>
    <ApplyVue
      :helloAppliVisibleProps="helloAppliVisible"
      @closeDialog="closeDialog"
    />
  </div>
</template>
<script>
import countDown from '@/components/CountDown.vue'
import ApplyVue from './Apply.vue'
import { timestampToTimes } from '@/utils/tools'
import { apiActivityListDetailByCode } from '@/apis/api'
export default {
  components: {
    countDown,
    ApplyVue
  },
  data() {
    return {
      ruleForm: {},
      labelText: '', // 倒计时组件的标题
      buttonText: '-', // 活动按钮的文字
      countDownTime: '0', // 倒计时时间
      helloAppliVisible: false,
    }
  },
  computed: {
    isMobile() {
      return this.$store.state.isMobile
    }
  },
  methods: {
    // 点击进入活动，跳转到登录页
    toLogin() {
      this.$router.push({
        path: '/login',
        query: {
          ...this.$route.query
        }
      })
    },
    timeEnd(e) {
      this.countDownTime = e;
    },
    onCopy() {
      if(!this.ruleForm.liveUrl) return;
      this.$copyText(this.ruleForm.liveUrl).then(() => {
        this.$message.success({message:"复制成功"})
      }).catch(() => {
        this.$message.error({message:"复制失败"})
      })
    },
    // 获取活动详情
    getDetail() {
      const params = {
        code: this.$route.query.code
      }
      apiActivityListDetailByCode(params).then((res) => {
        if (res) {
          // 存储活动ID
          this.$store.commit('setActivityId', res.result.id);
          window.localStorage.setItem('activityId', res.result.id);
          const data = res.result
          const curTime = new Date().getTime()// 当前时间戳
          if (curTime < data.enterFrom) { // 未到报名开始时间
            this.labelText = '距离报名开始'
            this.buttonText = '报名未开始'
            this.countDownTime = data.enterFrom
          } else if (curTime > data.enterFrom && curTime < data.enterTo) { // 在报名时间范围内
            this.labelText = '距离报名结束'
            this.buttonText = '报名'
            this.countDownTime = data.enterTo
          } else if (curTime > data.enterTo && curTime < data.activityFrom) { // 报名已经结束 活动未开始
            this.labelText = '距离活动开始'
            this.buttonText = '活动未开始'
            this.countDownTime = data.activityFrom
          } else if (curTime > data.activityFrom && curTime < data.activityTo) { // 活动进行中
            this.labelText = '距离活动结束'
            this.buttonText = '进入活动'
            this.countDownTime = data.activityTo
          } else if (curTime > data.activityTo) { // 活动已结束
            this.labelText = '活动已结束'
            this.buttonText = '活动已结束'
            this.countDownTime = '0'
          }
          this.ruleForm = Object.assign(data, {
            activityFrom: timestampToTimes(data.activityFrom),
            activityTo: timestampToTimes(data.activityTo),
            enterFrom: timestampToTimes(data.enterFrom),
            enterTo: timestampToTimes(data.enterTo),
          })
        }
      })
    },
    // 点击报名
    appliClickFn () {
      this.helloAppliVisible = true;
    },
    closeDialog (e) {
      if (e === 'helloAppliVisible') this.helloAppliVisible = false
    },
    enterActivityCloseFn() {
      this.$refs.bindFormRef.resetFields();
      this.enterActivityVisibleProps = false;
    }
  },
  mounted() {
    this.getDetail();
  }
}
</script>
<style lang='scss' scoped>
 .preview {
  padding: 40px 60px;
  width: 100%;
  height: 100%;
  color: #121A26;
  .activity-status {
    position: absolute;
    top: 0;
    right: 30px;
    z-index: 2;
  }
  .activity-top {
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.12);
    padding: 20px 40px;
    box-sizing: border-box;
    &-left {
      border-right: 1px dashed #8F8F8F;
      padding-right: 20px;
      box-sizing: border-box;
      >div:nth-of-type(2) {
        >div:nth-of-type(1) {
          display:  flex;
          flex-direction: column;
          justify-content: space-between;
          >p:nth-of-type(1) {
            font-size: 32px;
            font-weight: 600;
          }
          >p:nth-of-type(2) {
            font-size: 16px;
          }
        }
        >div:nth-of-type(2) {
          >p {
            font-size: 14px;
          }
        }
        
      }
    }
    &-right {
      padding-left: 20px;
      box-sizing: border-box;
      >div:nth-of-type(1) {
        height: 270px;;
        padding-top: 50px;
      }
      >div:nth-of-type(2) {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        i {
          color: #32cdcd;
          cursor: pointer;
        }
      }
    }
  }
}
.activity-tag {
  margin: 30px 0 10px 0;
  >div {
    display: inline-block;
    width: 7px;
    height: 30px;
    border-radius: 4px;
    background: #32cdcd;
    vertical-align: middle;
    margin-right: 10px;
  }
  span {
    font-size: 16px;
    line-height: 30px;
    display: inline-block;
    vertical-align: middle;
  }
}
.activity-tag-content {
  padding-left: 19px;
}
.activity-btn {
  margin: 0 auto;
  text-align: center;
  width: 197px;
  height: 40px;
  line-height: 40px;
  border-radius: 8px;
  margin-top: 30px;
  font-size: 20px;
  cursor: pointer;
}
.not-start-btn {
  background-color: #d0d3d6;
  color: #fff;
  cursor: not-allowed;
}
.active-btn {
  background-color: #32cdcd;
  color: #fff;
  cursor: pointer;
}
.mob-activity {
  background-color: #f4fbfb;
  width: 100%;
  height: 100%;
  position: relative;
  padding: 0 20px;
  .mob-countdown {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  >div {
    width: calc(100% - 40px);
    height: 90%;
    background-color: #fff;
    position: absolute;
    bottom: 0;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    padding: 20px 20px 0 20px;
    >h1 {
      line-height: 40px;
      text-align: center;
      font-size: 30px;
    }
    >p {
      color: #47454c;
      line-height: 35px;
      font-size: 24px;
      margin: 10px 0 20px 0;
    }
    .time-info {
      margin-top: 20px;
      font-size: 20px;
      line-height: 35px;
      color: #47454c;
      >div {
        display: inline-block;
      }
      >div:nth-of-type(1) {
        vertical-align: top;
        >p {
          width: 100%;
          line-height: 30px;
          margin-bottom: 10px;
        }
      }
      >div:nth-of-type(2) {
        text-align: center;
        vertical-align: middle;
      }
    }
  }
}
</style>